import React, { PureComponent } from "react";
import SquarePhotoMedia from "./Media";
import CardHover from "./CardHover";
import CardIcons from "../../Elements/CardIcons";
import { connect } from 'react-redux';
import { findNetworkDataFromWebFilters , findFromArray, removeTransFromCss, isPostAvailable, DetectPhone} from '../../../../utils'
import { themePostTracking, showPopUP, showReelPopUP } from '../../../../actions/themeActions'

class Card extends PureComponent {
  constructor(props) {
    super(props);
    this.myRef_square = React.createRef();
    this.state = {cardWidth : null,isMobile: DetectPhone()}
  }
  componentDidMount(){ 
    if(isPostAvailable(this.props.itemData.id)){
      this.onLoadPopup();
      setTimeout(()=>{
        const element = document.querySelector(`.ts_p_${this.props.itemData.id}`);
        if(element){element.scrollIntoView(true);}
      if(this.myRef_square && this.myRef_square.current) this.setState({cardWidth: this.myRef_square.current.clientWidth})
    },500)
    }
  }
  componentDidUpdate(){
    setTimeout(() => {
      if(this.myRef_square && this.myRef_square.current) this.setState({cardWidth: this.myRef_square.current.clientWidth})
    }, 1000);
  }

  onLoadPopup = event => {
    const { itemData, wall, postData, itemIndex, languageSetting, completeDataObject, wallId, webFilters } = this.props;
    let updatePostData = postData.map(itemData => completeDataObject[itemData])
    let filteredPostData = updatePostData.filter((filterData) => !String(filterData.id).includes("free_add_")) 
    let currentIndex = findFromArray(filteredPostData, itemData.id, itemIndex)

    if (wall.Personalization.postFeatured === 1) {
      removeTransFromCss(`add`);
      if(this.state.isMobile) this.props.showReelPopUP({ type: 'post', card: itemData, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, webFilters: webFilters, UserRule: wall.UserRule, wall: wall })
      else this.props.showPopUP({ type: 'post', card: itemData, idArray: filteredPostData, index: currentIndex, viewOnText: languageSetting.viewOnText, shareText: languageSetting.shareText, personalization: wall.Personalization, ThemeRule: wall.ThemeRule, webFilters: webFilters, UserRule: wall.UserRule, wall: wall })
    } themePostTracking({ action: 2, wall: wallId, feed: itemData.feedId, post: itemData.referenceId })
  }

  render() {
    const { itemData, wall, webFilters, adjustWidth,handleAllImageError, ownerId } = this.props;
    const highlight = itemData.highlight; const shareOption = wall.ThemeRule.shareOption; const hideContent = wall.ThemeRule.hideContent;
    const networkData = findNetworkDataFromWebFilters(itemData.networkId, webFilters)
    const ts_sp_post_in = { backgroundColor: wall.ThemeRule.cardColor };
    const postWidth = { width: highlight == 1 ? (adjustWidth * 2) : adjustWidth, height: highlight == 1 ? (adjustWidth * 2) : adjustWidth, padding: wall.Personalization.padding / 2 }
    const isShopIcon = (itemData.ugc_products && itemData.ugc_products.length > 0 && itemData.hotspot === 0) ? true : false;  
    const isVideo = (itemData.type===3 || itemData.type === 5) ? true : false;  
    const multiImages = (itemData.imageList && itemData.imageList.length > 0) ? true : false
    return (
      <div className={`ts_sp_post_wrapper ${itemData && itemData.id ? "ts_p_"+itemData.id:"" }`} style={postWidth}>
        <div className="ts_sp_post_in" onClick={itemData.isPost ? null : this.onLoadPopup} ref={this.myRef_square} style={ts_sp_post_in}>
          <div className="ts_sp_post_media_wrapp" >
              {isShopIcon || isVideo || multiImages ? <CardIcons item={itemData} cardWidth={this.state.cardWidth} wall={wall}/> :''}
              <SquarePhotoMedia handleAllImageError={handleAllImageError} item={itemData} wall={wall}/>
              <div className="ts_sp_post_details">
              {itemData.hotspot ?null: <CardHover cardWidth={this.state.cardWidth} ownerId={ownerId} item={itemData} wall={wall} network={networkData} />}
              </div>
          </div>
        </div>
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => { return { showPopUP: (data) => dispatch(showPopUP(data)),showReelPopUP: (data) => dispatch(showReelPopUP(data)) } }; 
const mapStateToProps = state => { return { wallId: state.appData.wallID, } }
export default connect(mapStateToProps, mapDispatchToProps)(Card);
